.category_bg {
  height: 100vh;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  overflow-y: auto;
  background-attachment: fixed;
}

.category_bg:before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
}

.category_top {
  padding-top: 10px;
}

.category_card {
  /* height: 150px;
  width: 150px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  opacity: 0.8;
  transition: transform 0.3s ease-in-out; */
  background-color: white;
  transition: transform 0.3s ease-in-out;
  border-radius: 10px;

}

.category_card:hover {
  transform: scale(1.1);
  transition: transform 0.3s ease-in-out;
}

.rest_bg {
  height: 130px;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 5px;
}

.rest_card {
  border: none;
}

.rest_name {
  font-size: 22px;
  color: red;
  font-family: sans-serif;
}

.badge-success {
  font-size: 20px;
}

.rest_add {
  color: grey;
}

.InfoBg {
  height: 70vh;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.categoryBody {
  text-align: center;
  font-size: 20px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.info_card {
  max-width: 1000px;
  margin: 0 auto;
  display: block;
  margin-top: 60vh;
}

.review_card {
  max-width: 812px;
}

.badge-success {
  color: #fff;
  background-color: #28a745;
  padding: 7px 18px;
  height: 34px;
}

.swiperCard {
  height: 200px;
  width: 100px;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.85;
  border-radius: 5px;
  overflow: hidden;
}

.swiperCard a {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.swiperCard:hover {
  opacity: 1;
}

.swiper-button-next .swiper-button-prev {
  height: 18px !important;
  width: 26px !important;
  outline: none;
}

.userImg {
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100px;
  width: 100px;
  float: right;
}

.usercard {
  margin-bottom: 10px;
  width: 100%;
  padding: 10px 0px;
}

.sticky {
  position: fixed;
  bottom: 1rem;
  right: 0;
}